import React from "react"
import {graphql, useStaticQuery, Link} from "gatsby"
import GraphImg from "graphcms-image"

import { Row, Col } from 'antd';
import Layout from '../components/Layout'
import { List, Avatar, Space, Card } from 'antd';


const pageQuery = graphql`
{
gcms {
    authors {
        biography
        id
        name
        picture {
        handle
        height
        width
        }
        title
    }
    }
}`

const About = () => {
  const {gcms: {authors}} = useStaticQuery(pageQuery)
  const { Meta } = Card;
  const gridStyle = {
    width: '100%',
    textAlign: 'center',
  };

  return (
    <Layout>
      <h1></h1>
      <p>Harvard Innovation Labs</p>
      <List
        grid={{ gutter: 16, column: 2 }}
        dataSource={authors}
        renderItem={author => (
          <List.Item>
            <Card
              hoverable
              style={gridStyle}
              cover={<GraphImg image={author.picture} maxWidth={300}/>}
            >
              <Meta title={author.name} description={author.biography} />
            </Card>
          </List.Item>
        )}
      />
    </Layout>
  )
}
export default About
